import { CardContent } from "@mui/material";
import { list } from "postcss";
import React from "react";
import { Card, CardHeader, CardTitle } from "react-bootstrap";
import { Clock, AlertTriangle, MapPin } from 'lucide-react';

const ShippingPolicy = () => {
  return (
    <div className="max-w-6xl mx-auto mt-5">
      <Card>
        <CardHeader>
          <CardTitle className="text-3xl font-bold text-center">Shipping Policy</CardTitle>
        </CardHeader>
        <CardContent className="space-y-6">
          <div className="space-y-3">
            <div className="flex items-center gap-2">
              <Clock className="h-5 w-5 text-blue-500" />
              <h3 className="text-lg font-semibold">Standard Policy</h3>
            </div>
            <p className="text-gray-700">
              Orders placed should be received within 24-36 Hours. Orders placed on public 
              holidays are handled on the next working day.
            </p>
          </div>
          <div className="space-y-3">
            <div className="flex items-center gap-2">
              <AlertTriangle className="h-5 w-5 text-amber-500" />
              <h3 className="text-lg font-semibold">Delayed or Lost Orders</h3>
            </div>
            <div className="text-gray-700 space-y-2">
              <p>
                A parcel is considered lost after it has been in the postal system for a 
                period of 4 business days. We cannot take responsibility for delays caused 
                by the courier.
              </p>
              <p>
                Although orders normally take much less than that we are unable to dispatch 
                a replacement until this period of time has elapsed.
              </p>
            </div>
          </div>

          <div className="space-y-3">
            <div className="flex items-center gap-2">
              <MapPin className="h-5 w-5 text-red-500" />
              <h3 className="text-lg font-semibold">Wrong or Incomplete Address</h3>
            </div>
            <div className="text-gray-700 space-y-2">
              <p>
                If your package is returned to us due to an incorrect or incomplete address, 
                you must pay the delivery cost to re-deliver your order to the correct/right 
                address.
              </p>
              <p>
                We are not responsible for the loss of your order if the address provided 
                at checkout is incomplete or incorrect.
              </p>
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  );
};

export default ShippingPolicy;